.Home {

    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 4vmin);
    // color: #ebebeb;    before video
    // color: #2c2c2c;
    color: hsla(0, 88%, 63%, 1);

    h1 {
        font-size: 108px;
        font-weight: bold;
    }

    @media (min-width: 1080px) {
        #background-video {
            position: fixed;
            right: 0;
            bottom: 0;
            min-width: 100%; 
            min-height: 100%;
            z-index: -1000;
        }        
    }

    @media (max-width: 1079px) {
        #background-video {
            position: fixed;
            right: 100;
            bottom: 0;
            min-width: 100%; 
            min-height: 100%;
            z-index: -1000;
        }        
    }    
    .darkmode {
        opacity: 1;
    }
    .brightmode {
        opacity: 0.6;
    } 
    // toggle input
    .switch {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 34px;
      }
    
    .switch input { 
        opacity: 0;
        width: 0;
        height: 0;
      }
      
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
    }
      
    .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }
      
    input:checked + .slider {
        background-color: #2196F3;
    }
      
    input:focus + .slider {
        box-shadow: 0 0 1px #2196F3;
    }
      
    input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }
      
      /* Rounded sliders */
    .slider.round {
        border-radius: 34px;
    }
      
      .slider.round:before {
        border-radius: 50%;
    }
    
}